@import url(../atoms/container.css);
@import url(../atoms/heading.css);
@import url(../molecules/product-info.css);

.product {
	font-family: var(--font-family-primary);
	color: var(--color-light-primary);
}

.product__info-wrapper {
	display: flex;
	gap: 1rem;
}

@media (width < 728px) {
	.product__info-wrapper {
		flex-direction: column;
	}
}

@media (width >= 728px) {
	.product__info-wrapper {
		align-items: flex-start;
	}
}
