.product-info {
	padding: 0.5rem;
	box-sizing: border-box;
	background-color: var(--color-dark-secondary);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	row-gap: 1rem;
	border-radius: var(--border-radius-primary);
}

@media (width >= 728px) {
	.product-info:not(.product-info--secondary) {
		width: 25rem;
	}
}

@media (width >= 728px) {
	.product-info--secondary {
		padding: 1rem;
		margin-left: auto;
	}
}

.product-info__img {
	display: block;
	object-fit: contain;
}

.product-info__img:not(.product-info__img--extra) {
	margin: 0 auto;
}

@media (width < 728px) {
	.product-info__img {
		height: 15rem;
	}
}

@media (728px <= width < 1024px) {
	.product-info__img {
		width: 17rem;
		height: 17rem;
	}
}

@media (width >= 1024px) {
	.product-info__img {
		width: 24rem;
		height: 24rem;
	}
}

.product-info__extra {
	display: flex;
	column-gap: 0.5rem;
	overflow: auto;
}

.product-info__img--extra {
	width: 4rem;
	height: 4rem;
	flex-shrink: 0;
	border-radius: var(--border-radius-primary);
	background-color: var(--color-dark-primary);
}

.product-info__total-price {
	font-size: 2rem;
	line-height: 2.5rem;
}

.product-info__button {
	align-self: flex-start;
}
