@import url(../atoms/heading.css);

.specifications-table {
	border-collapse: collapse;
}

.specifications-table__header {
	display: block;
	margin-bottom: 0.5rem;
}

.specifications-table__body {
	display: flex;
	flex-direction: column;
	row-gap: 0.5rem;
}

.specifications-table__row:not(.specifications-table__row--header) {
	border-bottom: 0.0625rem solid currentColor;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.specifications-table__heading {
	padding: 0;
}

.specifications-table__heading:not(.specifications-table__row--header *) {
	font-size: 1rem;
	line-height: 1.5rem;
	font-weight: 400;
}

.specifications-table__data {
	padding: 0;
}
